import React from "react";
import { autoInput } from "../../AutoInput.js";
import type { AutoRichTextInputProps } from "../../shared/AutoRichTextInputProps.js";

const LazyLoadedPolarisAutoRichTextInput = React.lazy(async () => {
  try {
    return await import("./PolarisAutoRichTextInput.js");
  } catch (error) {
    console.warn("Optional dependency not found, some features may not be available.");
    throw error;
  }
});

export const PolarisAutoRichTextInput = autoInput((props: AutoRichTextInputProps) => {
  return (
    <>
      <LazyLoadedPolarisAutoRichTextInput {...props} />
    </>
  );
});
